@import "./theme/breakpoint";
@import "./theme/mixin_extend";
@import "./theme/layout";
@import "./theme/font";
@import "./theme/variables";
@import "./theme/global";

body {
    margin: 0;
    font-family:
        var(--font-family),
        -apple-system,
        BlinkMacSystemFont,
        "Segoe UI",
        "Roboto",
        "Oxygen",
        "Ubuntu",
        "Cantarell",
        "Fira Sans",
        "Droid Sans",
        "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

